import React from 'react';

export default class Pokemon extends React.Component {
  state = {
    evolutionChain: [],
    pokedexEntry: '',
    kind: '',
  };

  // For Fetching entries & pokemon 'king' (for example: seed pokemon)
  // https://pokeapi.co/api/v2/pokemon-species/2/

  // For evolution chains:
  // https://pokeapi.co/api/v2/evolution-chain/1/

  render() {
    const {
      name,
      index,
      types,
      description,
      stats,
      height,
      weight,
      eggGroup,
      abilities,
      genderRatioMale,
      genderRatioFemale,
      evs,
      hatchSteps,
    } = this.props.pokemon;

    return (
      <React.Fragment>
        <div className="pokemonCardTop">
          <h3>#{index.padStart(3, '00')}</h3>
          <h3>{name}</h3>
        </div>
        <div className="pokemonCardBot">
          <div className="typing">
            {types.map(({type: {name}}) => (
              <span className={name} key={name} />
            ))}
          </div>
          <div className="pokemonSprite">
            <img
              src={`http://cursist38.reacollege.eu/sprites/${index}.png`}
              alt={name + 'sprite'}
            />
            <img
              src={`http://cursist38.reacollege.eu/sprites/shiny/${index}.png`}
              alt={name + 'sprite'}
            />
          </div>
          <div className="pokemonStats">
            {stats.map(stat => (
              <div className={stat.stat.name} key={stat.stat.name}>
                <span>{stat.stat.name}</span>
                <div className="progress">
                  <div
                    style={{width: stat.base_stat / 2 + '%'}}
                    className={stat.stat.name + 'value'}
                  >
                    <span>{stat.base_stat}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <p>Click anywhere to close the screen!</p>
        </div>
      </React.Fragment>
    );
  }
}
